"use client";

import { useLanguage } from "@/context/LanguageContext";

export default function LanguageToggle() {
  const { lang, setLang, ready } = useLanguage();
  if (!ready) return null;

  return (
    <div className="flex items-center gap-1 rounded-full bg-[#0A5F91] p-1">
      <button
        onClick={() => setLang("so")}
        className={`px-2 py-1 rounded-full text-[11px] font-extrabold transition ${
          lang === "so" ? "bg-white text-[#0B6EA9]" : "text-white/90"
        }`}
        aria-label="Somali"
        title="Somali"
      >
        ðŸ‡¸ðŸ‡´
      </button>

      <button
        onClick={() => setLang("en")}
        className={`px-2 py-1 rounded-full text-[11px] font-extrabold transition ${
          lang === "en" ? "bg-white text-[#0B6EA9]" : "text-white/90"
        }`}
        aria-label="English"
        title="English"
      >
        ðŸ‡¬ðŸ‡§
      </button>
    </div>
  );
}
